@charset "UTF-8";
/* Указываем box sizing */
*,
*::before,
*::after {
  box-sizing: border-box; }

/* Убираем внутренние отступы */
ul[class],
ol[class] {
  padding: 0; }

/* Убираем внешние отступы */
body,
h1,
h2,
h3,
h4,
p,
ul[class],
ol[class],
li,
figure,
figcaption,
blockquote,
dl,
dd {
  margin: 0; }

/* Выставляем основные настройки по-умолчанию для body */
body {
  min-height: 100vh;
  scroll-behavior: smooth;
  text-rendering: optimizeSpeed;
  line-height: 1.5; }

/* Удаляем стандартную стилизацию для всех ul и il, у которых есть атрибут class*/
ul[class],
ol[class] {
  list-style: none; }

/* Элементы a, у которых нет класса, сбрасываем до дефолтных стилей */
a:not([class]) {
  text-decoration-skip-ink: auto; }

/* Упрощаем работу с изображениями */
img {
  max-width: 100%;
  display: block; }

/* Указываем понятную периодичность в потоке данных у article*/
article > * + * {
  margin-top: 1em; }

/* Наследуем шрифты для инпутов и кнопок */
input,
button,
textarea,
select {
  font: inherit; }

/* Удаляем все анимации и переходы для людей, которые предпочитай их не использовать */
@media (prefers-reduced-motion: reduce) {
  * {
    animation-duration: 0.01ms !important;
    animation-iteration-count: 1 !important;
    transition-duration: 0.01ms !important;
    scroll-behavior: auto !important; } }

.sprite {
  display: none; }

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  overflow: hidden;
  white-space: nowrap;
  border: 0;
  clip: rect(0 0 0 0);
  clip-path: inset(100%); }

body {
  padding: 0;
  font-style: normal;
  font-weight: normal;
  font-size: 16px;
  line-height: 26px;
  font-family: Roboto, Arial, sans-serif;
  color: #727272;
  background-color: #ffffff; }

a {
  text-decoration: none; }

.container {
  width: 1920px;
  margin: 0 auto;
  padding: 0 20px; }

.header {
  padding-top: 49px;
  background-color: #EEEFF1; }

.navigation {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  align-items: center;
  font-weight: 500;
  line-height: 30px;
  color: #000000;
  text-transform: uppercase; }

.navigation a {
  display: block;
  color: #000000;
  user-select: none; }

.navigation a:hover,
.navigation a:focus {
  color: #fb565a; }

.navigation a:active {
  color: rgba(0, 0, 0, 0.3); }

.navigation-current a {
  border-bottom: 2px solid #fb565a; }

.navigation-current a:hover,
.navigation-current a:focus {
  color: #000000; }

.logo {
  width: 160px;
  height: 65px;
  margin-right: 202px;
  font-family: Revalia;
  font-style: normal;
  font-weight: normal;
  font-size: 48px;
  line-height: 60px;
  /* identical to box height */
  text-align: center;
  color: #34547A; }

.menu {
  display: flex;
  flex-wrap: wrap;
  margin: 0;
  padding: 0;
  list-style: none; }

.menu a {
  display: block;
  margin-right: 46px;
  padding-top: 22px;
  padding-bottom: 4px; }

.services {
  display: flex;
  justify-content: space-between;
  width: 100%;
  min-height: 528px;
  margin: 0;
  padding: 0;
  border-bottom: 2px solid #eeeeee;
  background-color: #EEEFF1; }

.col-left {
  width: 360px;
  margin: 0;
  padding: 0;
  padding-top: 41px; }

.col-right {
  width: 541px;
  margin: 0;
  padding: 0;
  padding-top: 75px; }

.col-right b {
  display: block;
  margin: 0;
  margin-bottom: 33px;
  padding: 0;
  font-weight: 500;
  font-size: 48px;
  line-height: 79px;
  color: #000000; }

.col-right p {
  margin: 0;
  margin-bottom: 39px;
  padding: 0; }

.achievements {
  display: flex;
  background-color: #34547A; }

.achievements-content {
  display: flex;
  max-width: 141px;
  margin: 0;
  margin-left: auto;
  padding: 0;
  padding-top: 10px; }

.achievements-content a {
  position: relative;
  padding: 100px 0;
  padding-left: 60px;
  color: #ffffff; }

.achievements-content a::before {
  content: "";
  position: absolute;
  top: 101px;
  left: 0;
  width: 50px;
  height: 50px;
  background-image: url("/img/project-icon.png");
  background-repeat: no-repeat; }

.quantity {
  font-weight: 500;
  font-size: 21px;
  line-height: 25px; }

.button {
  display: block;
  width: 225px;
  min-height: 65px;
  padding: 20px 0;
  text-align: center;
  letter-spacing: 0.1em;
  color: #ffffff;
  text-transform: uppercase;
  background-color: #34547A; }
